@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");

h2 {
  /* font-family: "Josefin Sans", cursive; */
  font-family: "Dancing Script", cursive;
  text-align: center;
  background-color: rgb(110, 184, 147);
  margin-top: 0;
  padding: 15px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: center;
}

.memory,
.losing {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 40%;
  justify-content: center;

  box-shadow: 0 0 10px rgb(4, 110, 78);
}

.memory {
  margin: auto;
}

.time,
.players {
  display: flex;
  flex-direction: column;
  width: 20%;
  justify-content: center;
  align-items: center;
  margin: auto;
  box-shadow: 0 0 10px rgb(4, 110, 78);
  padding: 20px;
}

.getTime {
  font-family: "Dancing Script", cursive;
  font-size: 26px;
}

.setTime {
  font-family: "Dancing Script", cursive;
  margin-top: 20px;
}

.setTime-item input {
  font-family: "Dancing Script", cursive;
  padding: 10px;
  font-size: 20px;
  width: 22%;
  background-color: rgb(110, 184, 147);
  border: none;
}

.setTime-item-p {
  text-align: center;
  font-family: "Dancing Script", cursive;
  font-size: 28px;
  font-weight: bold;
}

.cell {
  width: 33%;
  height: 120px;
}

button:hover {
  background-color: rgb(194, 226, 210);
  border: none;
}

.losing {
  font-family: "Dancing Script", cursive;
  flex-direction: column;
  align-items: center;
  color: red;
  margin-bottom: 20px;
}

.players {
  font-family: "Dancing Script", cursive;
}

.player-1 h3:first-of-type,
.player-2 h3:first-of-type {
  font-size: 25px;
}

input[type="text"] {
  font-family: "Dancing Script", cursive;
  margin-top: 10px;
  background-color: rgb(110, 184, 147);
  border: none;
  padding: 10px;
  font-size: 20px;
}

.player-1 {
  padding-bottom: 20px;
}

@media only screen and (min-width: 300px) and (max-width: 992px) {
  .memory {
    width: 90%;
    margin-bottom: 20px;
  }
  .cell {
    height: 100px;
  }

  .container {
    flex-direction: column;
  }

  .time {
    flex-direction: column;
    width: 80%;
    margin-bottom: 20px;
  }

  .players {
    width: 80%;
    margin-bottom: 20px;
  }
}
